import {React, useState, useEffect} from 'react'
import Basic from '../Icons/Icon'
import Navheader from './Navheader'
import Button from '@material-ui/core/Button';
import CloseIcon from '@material-ui/icons/Close';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import PermIdentityIcon from '@material-ui/icons/PermIdentity';
import { duration } from '@material-ui/core';


function Presentation() {
    const [show, setshwow]=useState(false)

    let windowclose =()=>{
        let element = document.getElementById("closwind");
        element.animate([
            {opacity: '2px'},
            {transform:'translateY(-30px)'}
        ],{
            duration:10000,
            iterations : Infinity
        });
        setshwow(false);

    }
    /*---------FORMULAIRE DE CONTACT------------*/
    
    return (
        <>
            {
                show?<div className='containercontact' id="closwind">
                    <div className="itemcontact" >
                        <div className='icon'>
                            <CloseIcon onClick={()=>windowclose()} id="close"/>
                        </div>
                        <div className="item3">
                            <Basic />
                        </div>
                    </div>
                </div>:null
              }

                               
        <section className="Presentationsection">
            <Navheader />
            <div className="Presentation">
            <div className="item2">
                <article>
                Hello,<br></br>Je suis CHidiac<br></br>Administrateur Réseaux <br></br> développeur freelance à temps partiel
                </article>
                <Button className="buttpresentation" variant="contained" color="F48C06" onClick={()=>setshwow(true)}>Contact me</Button>
                <Button variant="contained" color="#F48C06" className="buttpresentation">Télécharger mon CV</Button>
                <div className="presentationiconsocialnetwork">
                    <ArrowDropDownIcon />
                    <AddCircleIcon />
                    <PermIdentityIcon />
                </div>
            </div>
            <div className="item2">Photo</div>
            </div>
        </section>
        </>
    )
}

export default Presentation
