
import React from 'react';
import { useFormik } from 'formik';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import Button from '@material-ui/core/Button';
const useStyles = makeStyles((theme) => ({
    root: {
      '& > *': {
        margin: theme.spacing(1),
        width: '25ch',
      },
    },
  }));

 
 
const Basic = () => {
  // Note that we have to initialize ALL of fields with values. These
  // could come from props, but since we don’t want to prefill this form,
  // we just use an empty string. If we don’t do this, React will yell
  // at us.
  const formik = useFormik({
    initialValues: {
      firstName: '',
      number: '',
      email: '',
    },
    onSubmit: values => {
      alert(JSON.stringify(values, null, 2));
    },
  });
   const classes = useStyles();
  return (
    <form onSubmit={formik.handleSubmit} className={classes.root} noValidate autoComplete="off">
      <TextField id="lastName" name ='lastName' label="votre Nom et Prenom(s) svp" type="text"  variant="outlined" color="secondary" onChange={formik.handleChange}
        value={formik.values.lastName} />
    <br></br>
     <TextField
        id="number"
        name ='number'
        type='tel'
        label="Votre Numéro de téléphone svp"
        variant="outlined"
        color="secondary"
        onChange={formik.handleChange}
        value={formik.values.number}
      />
      <br></br>
      <TextField
        id="email"
        name ='email'
        type="email"
        label="Votre e-mail svp"
        variant="outlined"
        color="secondary"
        onChange={formik.handleChange}
        value={formik.values.email}
      />
       <br></br>
        <TextField
        id="outlined-secondar"
        label="Objet du message"
        variant="outlined"
        color="secondary"
      />
     <br></br>
     <TextareaAutosize id="outlined-secondary" className="MuiFormControl" aria-label="minimum height" rowsMin={3} placeholder="Maximun 10 lignes pour votre message" />

     <Button variant="contained" color="secondary" type="submit">
        Envoyer
    </Button>
    </form>
  );
}
export default Basic;